import {LOGGING_ENABLED} from "@/config/config";
// export const LOGGING_ENABLED = true; // Set to false to disable logs

// Centralized logging function
export const log = (message: any, ...optionalParams: any[]): void => {
  if (LOGGING_ENABLED) {
    console.log(message, ...optionalParams);
  }
};

export const logError = (message: any, ...optionalParams: any[]): void => {
  if (LOGGING_ENABLED) {
    console.error(message, ...optionalParams);
  }
};

export const logWarning = (message: any, ...optionalParams: any[]): void => {
  if (LOGGING_ENABLED) {
    console.warn(message, ...optionalParams);
  }
};
