// Buat router
const router = createRouter({
  history: createWebHistory("/office/"),
  routes,
});

// Middleware untuk autentikasi sebelum masuk ke halaman tertentu
router.beforeEach((to, from, next) => {
  const token = localStorage.getItem("token");
  // console.log("TOKEN : ", token);
  let loggedIn = false;
  if (to.path == "/login" && token) loggedIn = true;
  else if (("noAuth" in to.meta)) {
    if (from.path != "/" && from.path != to.path) {
      window.location.reload();  // Force page reload
      return;
    }
    return next();
  } else if (!checkToken()) {
    // need login first
    console.warn("🔒 Akses ditolak! Harus login dulu.");
    localStorage.removeItem("token");
    return next("/login");
  }

  const decoded = jwtDecode(token);
  const user_level = parseInt(decoded.user_level, 10);
  const permissions = decoded.permissions;

  if (user_level == 0) return next();
  // log("TO : ", { to: to });
  if (from.name == "login" || to.path == "/dashboard" || loggedIn) {
    //wants to redirect to specific page after login
    if (user_level == 9) {
      if (to.path == "/absensi") return next();
      return next("/absensi");
    }
    if (user_level == 8) {
      //let level_8.ts handle the routing
      return handleLevel8Routing(to, from, next, decoded);
    }
  }
  const navItem = navItems.find(item => item.to === to.path);

  if (navItem) {
    const hasAccess = canAccess(user_level, permissions, navItem);
    if (hasAccess){
      // console.log(`Access to ${to.path}:`, hasAccess);
      return next();
    }
    else {
      console.warn(`⛔ Akses ditolak! Hubungi admin jika membutuhkan izin`);
      return next("/error/403");
    }
  } else {
    // console.log(`Nav item for ${to.path} not found.`);
    return next();
  }

  // if (to.meta.minimumLevel && user_level > (to.meta.minimumLevel as number)) {
  //   console.warn(`⛔ Akses ditolak! Hubungi admin jika membutuhkan izin`);
  //   return next("/error/403");
  // } 
  // if (to.meta.hasPermissions) {
  //   if(!decoded.permissions) {
  //     console.warn(`⛔ Akses ditolak! Tidak punya izin yang cukup`);
  //     return next("/error/403");
  //   }
  //   const requiredPermissions = to.meta.hasPermissions as string[];
  //   const hasAllPermissions = requiredPermissions.every(p => decoded.permissions.includes(p));
  //   if (!hasAllPermissions) {
  //     console.warn(`⛔ Akses ditolak! Tidak punya izin yang cukup`);
  //     return next("/error/403");
  //   }
  // }
  return next();
});

export default router;
