<script setup>
import { ref, onMounted, computed } from "vue";
import { useRouter } from "vue-router";
import { navItems, canAccess } from '@/router/access';
import { log } from "@/utils/logger";
import { PHOTO_BASE_URL } from "@/config/config";
import userPhoto from "@images/logo.png";
import DarkModeToggle from '@/components/DarkModeToggle.vue'

import { useUserStore } from "@/stores/user";
import { useThemeStore } from '@/stores/theme';
const theme = useThemeStore()
const userData = useUserStore();
theme.load()

const router = useRouter();
const isLoggedIn = ref(false);
const userName = ref("User");
//const userPhoto = ref("src/assets/images/avatars/10.png");

const visibleNavItems = computed(() => {
  if (!isLoggedIn.value) return [];
  return navItems.filter(item => {
    const showItem = item.isHeader === true;
    return showItem && canAccess(userData.level, userData.permissions, item)
  });
});

const checkLoginStatus = () => {
  const token = localStorage.getItem("token");
  if (token) {
    isLoggedIn.value = true;
    userName.value = localStorage.getItem("user_name") || "User";
    //userPhoto.value = localStorage.getItem("user_photo") || "src/assets/images/avatars/10.png";
  }
};

const logout = () => {
  localStorage.removeItem("token");
  localStorage.removeItem("user_name");
  localStorage.removeItem("user_photo");
  userData.clearUser();
  isLoggedIn.value = false;
  router.push("/login");
};

// const name = "absentest_1745545977.png";
const name = "user10_1744855110.jpeg";
const url = userData.photo;

onMounted(() => {
  checkLoginStatus();
  const userAgent = navigator.userAgent.toLowerCase();
  const width = window.innerWidth;
  const height = window.innerHeight;
  // log("User : ", { height: height, width: width, "user-agent": userAgent });
  // log("Photo : ", {"userData.photo": userData.photo});
  // console.log("Visible Nav : ", visibleNavItems.value);
});

</script>

<template>
  <nav :class="['navbar', 'navbar-expand-lg', 'fixed-top', 'navbar-global']">

    <div class="container">
      <router-link class="navbar-brand" to="#">
        <img src="@images/logo.png" alt="Logo" class="logo" style="width: 120px; height: auto" />
      </router-link>

      <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div
        class="collapse navbar-collapse ['dropdown-menu', 'dropdown-menu-end', 'shadow', theme.darkMode ? 'bg-dark text-white' : 'bg-white text-dark']"
        id="navbarNav">
        <ul class="navbar-nav me-auto">
          <li class="nav-item" v-for="item in visibleNavItems" :key="item.to">
            <router-link class="nav-link" :to="item.to">
              {{ item.label }}
            </router-link>
          </li>
        </ul>

        <!-- dark mode -->
        <div class="me-4 mt-3">
          <DarkModeToggle />
        </div>

        <!-- User Dropdown -->
        <ul class="navbar-nav">
          <li class="nav-item dropdown" v-if="isLoggedIn">
            <a href="javascript:;" class="nav-link dropdown-toggle" data-bs-toggle="dropdown">
              <img :src="url" alt="User" class="rounded-circle" width="45" height="45" />
            </a>
            <div :class="[
              'dropdown-menu',
              'dropdown-menu-end',
              'shadow',
              theme.darkMode ? 'bg-dark text-white' : 'bg-white text-dark'
            ]">
              <div class="text-center p-3">
                <img :src="url" class="rounded-circle p-1 shadow mb-3" width="90" height="90" alt="User" />
                <h5 class="user-name mb-0 fw-bold">Hello, {{ userData.username }}</h5>
              </div>
              <hr class="dropdown-divider" :class="theme.darkMode ? 'border-light' : 'border-dark'" />

              <router-link class="dropdown-item d-flex align-items-center gap-2 py-2"
                :class="theme.darkMode ? 'text-white' : 'text-dark'" to="/profile">
                <i class="material-icons-outlined"></i>Profile
              </router-link>

              <router-link class="dropdown-item d-flex align-items-center gap-2 py-2"
                :class="theme.darkMode ? 'text-white' : 'text-dark'" to="#">
                <i class="material-icons-outlined"></i>Dashboard
              </router-link>  

              <hr class="dropdown-divider" :class="theme.darkMode ? 'border-light' : 'border-dark'" />

              <a href="javascript:;" @click="logout" class="dropdown-item d-flex align-items-center gap-2 py-2"
                :class="theme.darkMode ? 'text-white' : 'text-dark'">
                <i class="material-icons-outlined"></i>Logout
              </a>
            </div>
          </li>
        </ul>


        <!-- Login Button (Jika Belum Login) 
        <router-link v-if="!isLoggedIn" class="btn btn-primary me-2" to="/login"
          >Login</router-link
        >-->
      </div>
    </div>
  </nav>
</template>

<style scoped>
.user-photo {
  border-radius: 50%;
  object-fit: cover;
}

.dropdown-toggle::after {
  display: none;
}

.dropdown-menu {
  width: 220px;
}

.dropdown-item i {
  font-size: 20px;
}

.dropdown-item:hover {
  background: #dc3545;
  color: var(--dropdown-hover-text, white);
}

.dropdown-menu {
  background: white !important;
  box-shadow: none !important;
}

.nav-item.dropdown {
  background: transparent !important;
  box-shadow: none !important;
  padding: 0 !important;
  border: none !important;
}

.nav-item.dropdown .dropdown-toggle {
  background: transparent !important;
  box-shadow: none !important;
  padding: 0 !important;
  border: none !important;
  outline: none !important;
}

.nav-item.dropdown .dropdown-toggle img {
  background: transparent !important;
  box-shadow: none !important;
  border: none !important;
}

.navbar-nav .nav-link {
  transition: background-color 0.3s ease, color 0.3s ease;
  padding: 8px 15px;
  border-radius: 5px;
}

/* Hover effect */
.navbar-nav .nav-link {
  position: relative;
}

.navbar-nav .nav-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 15%;
  width: 70%;
  height: 2px;
  background-color: #dc3545;
  transform: scaleX(0);
  transition: transform 0.3s ease;
  transform-origin: left;
}

.navbar-nav .nav-link:hover::after {
  transform: scaleX(1);
}
</style>
