import { NavigationGuardNext, RouteLocationNormalized } from "vue-router";

interface DecodedToken {
  permissions: string[];
  [key: string]: any;
}

const permissionRedirectMap: Record<string, string> = {
  "admin_site_de": "/de/admin",
  "admin_trja_de": "/de/down-equipment",
  "admin_absensi": "/hr/absensi",
};
9
export default function handleLevel8Routing(
  to: RouteLocationNormalized,
  from: RouteLocationNormalized,
  next: NavigationGuardNext,
  decoded: DecodedToken
) {
  const permission = decoded.permissions?.[0]; // Level 8 only has one
  const targetPath = permissionRedirectMap[permission];
  console.log("FROM : ", from);
  console.log("TO   : ", to);
  if (!targetPath) {
    console.warn("🚫 No redirect path found for permission:", permission);
    return next(); // allow to continue if nothing matches
  }

  // Prevent redirecting to the same route
  if (to.path === targetPath) {
    return next(); // already there
  }

  return next(targetPath); // do redirect
}
