import { defineStore } from 'pinia'
import { PHOTO_BASE_URL } from "@/config/config";

export const useUserStore = defineStore('user', {
  state: () => ({
    username: '',
    level: '',
    permissions: [],
    name: '',
    photo: '',
    token: localStorage.getItem('jwt') || '',
    baseUrl: 'https://www.transkon-rent.com',
    apiBaseUrl: 'https://www.transkon-rent.com/api',
    background: localStorage.getItem('background') || '#ffffff', // default background
    font: localStorage.getItem('font') || 'Arial', // default font
    darkMode: localStorage.getItem('darkMode') === 'true',
  }),

  actions: {
    setUser({ username, photo, level, permissions, token }) {
      this.username = username
      this.photo = PHOTO_BASE_URL + "/profile/" + photo
      this.level = level
      this.permissions = permissions
      this.token = token
      localStorage.setItem('token', token)
    },
    toggleDarkMode() {
      this.darkMode = !this.darkMode;
      localStorage.setItem('darkMode', this.darkMode);
    },

    setTheme({ background, font }) {
      this.background = background;
      this.font = font;
    },

    setProfile(photo) {
      if(photo){
        this.photo = PHOTO_BASE_URL + "/profile/" + photo
      }
    },

    clearUser() {
      this.username = ''
      this.level = ''
      this.role = ''
      this.permissions = []
      this.token = ''
      localStorage.removeItem('token')
    }
  },
  persist: true
})
