// Function to check access for each item
export const canAccess = (
  userLevel: number,
  userPermissions: string[],
  item: NavItem
): boolean => {
  // log("canAccess : ", { userLevel: userLevel });
  // log("canAccess : ", { userPermissions: userPermissions });
  return (
    userLevel <= (item.minLevel ?? defaultMinLevel) &&
    (item.requiredPermissions ?? []).every(p => userPermissions.includes(p)) &&
    (item.notPermissions ?? []).every(p => !userPermissions.includes(p))
  );
};

// Function to check token expiry (unchanged)
export const checkToken = (): boolean => {
  const token = localStorage.getItem("token");

  if (!token) return false;

  try {
    const decoded = jwtDecode(token);
    const expiryTime = decoded.exp;
    const currentTime = Math.floor(Date.now() / 1000);
    return expiryTime > currentTime;
  } catch (error) {
    console.error("Invalid token format", error);
    return false;
  }
};

// Function to group nav items by category
const groupNavItemsByCategory = (items: NavItem[]) => {
  const groupedItems: Record<string, NavItem[]> = {};

  items.forEach(item => {
    const category = item.category || 'Other'; // Default to 'Uncategorized' if no category exists
    if (!groupedItems[category]) {
      groupedItems[category] = [];
    }
    groupedItems[category].push(item);
  });

  return groupedItems;
};

// Group nav items by category
export const groupedNavItems = groupNavItemsByCategory(navItems);

// Example function to render grouped nav items (could be a template in Vue)
export const renderNavItems = (userLevel: number, userPermissions: string[]) => {
  const grouped = groupedNavItems;

  return Object.keys(grouped)
    .map(category => {
      // Filter out items that the user doesn't have access to
      const items = grouped[category].filter(item => {
        const showItem = item.isHeader === true;
        return showItem && canAccess(userLevel, userPermissions, item);
      });

      // Only return categories that have accessible items
      if (items.length > 0) {
        return {
          category,
          items,
        };
      }
      return null;
    })
    .filter(group => group !== null); // Remove null categories
};

