<template> 
    <button @click="toggleDarkMode" class="btn btn-outline-secondary" :title="isDark ? 'Light Mode' : 'Dark Mode'">
      <transition name="rotate-fade" mode="out-in">
        <i :key="isDark" :class="isDark ? 'bi bi-moon-fill' : 'bi bi-sun-fill'"></i>
      </transition>
    </button>
  </template>
  
  <script setup>
  import { computed } from 'vue'
  import { useThemeStore } from '@/stores/theme'
  const theme = useThemeStore()
  theme.load()
  
  const toggleDarkMode = () => theme.toggle()

  const isDark = computed(() => theme.darkMode)
  </script>
  
  <style scoped>
  button {
    border: none;               
    background: transparent; 
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  /* Fade + rotate transition */
  .rotate-fade-enter-active, .rotate-fade-leave-active {
    transition: opacity 0.4s ease, transform 0.4s ease;
  }
  .rotate-fade-enter-from {
    opacity: 0;
    transform: rotate(-180deg) scale(0.5);
  }
  .rotate-fade-leave-to {
    opacity: 0;
    transform: rotate(180deg) scale(0.5);
  }
  </style>
  

  