<!-- src/components/FontProvider.vue -->
<template>
    <div :style="fontStyle" class="font-provider">
      <slot />
    </div>
  </template>
  
  <script setup>
  import { computed } from 'vue'
  import { useFontStore } from '@/stores/font'
  
  const fontStore = useFontStore()
  
  const props = defineProps({
    family: {
      type: String,
      default: 'Inter'
    },
    weight: {
      type: String,
      default: '400'
    }
  })
  
  const fontStyle = computed(() => ({
    '--current-font': `'${fontStore.fontFamily || props.family}', sans-serif`,
    '--current-font-weight': fontStore.fontWeight || props.weight,
    'font-family': `'${fontStore.fontFamily || props.family}', sans-serif`,
    'font-weight': fontStore.fontWeight || props.weight
  }))
  </script>
  
  <style>
  /* Impor font dari Google Fonts */
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&family=Poppins:wght@300;400;600&family=Roboto:wght@300;400;500&display=swap');
  
  :root {
    --current-font: 'Inter', sans-serif;
    --current-font-weight: 400;
  }
  
  body {
    font-family: var(--current-font);
    font-weight: var(--current-font-weight);
    transition: font-family 0.3s ease;
  }
  
  .font-provider {
    font-family: inherit;
  }
  </style>