// src/stores/font.js
import { defineStore } from 'pinia'

export const useFontStore = defineStore('font', {
  state: () => ({
    fontFamily: 'Inter',
    fontWeight: '400'
  }),
  actions: {
    setFont(family, weight = '400') {
      this.fontFamily = family
      this.fontWeight = weight
    }
  }
})