<template>
    <button
      :type="type"
      class="btn"
      :class="[
        `btn-${color}`,
        sizeClass,
        { 'd-block w-100': block }
      ]"
      :style="combinedStyles"
    >
      <slot></slot>
    </button>
  </template>
  
  <script setup>
  import { computed } from 'vue';
  import { useFontStore } from '@/stores/font';
  
  const fontStore = useFontStore();
  
  const props = defineProps({
    type: {
      type: String,
      default: 'button',
    },
    color: {
      type: String,
      default: 'primary',
    },
    size: {
      type: String,
      default: 'md',
      validator: (value) => ['sm', 'md', 'lg'].includes(value)
    },
    block: {
      type: Boolean,
      default: false,
    },
    customStyle: {
      type: Object,
      default: () => ({}),
    },
    padding: {
      type: String,
      default: '',
    },
  });
  
  const sizeClass = computed(() => {
    if (props.size === 'sm') return 'btn-sm';
    if (props.size === 'lg') return 'btn-lg';
    return '';
  });

  //font
  const combinedStyles = computed(() => ({
    fontFamily: fontStore.fontFamily || 'Inter',
    fontWeight: fontStore.fontWeight || '400',
    padding: props.padding,
    ...props.customStyle
  }));
  </script>
  
  <style scoped>
  .btn {
    font-family: inherit;
    font-weight: inherit;
    transition: all 0.3s ease;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  /* Warna hover spesifik */
  .btn-primary:hover {
    background-color: #0b5ed7 !important;
    transform: translateY(-1px);
  }
  
  .btn-danger:hover {
    background-color: #bb2d3b !important;
    transform: translateY(-1px);
  }
  .btn-secondary:hover {
    background-color: #525252 !important;
    transform: translateY(-1px);
  }
  
  /* Ukuran custom */
  .btn-sm { 
    min-width: 80px; 
    height: 32px; 
    font-size: 12px; 
  }
  
  .btn-md { 
    min-width: 120px; 
    height: 40px; 
    font-size: 14px; 
  }
  
  .btn-lg { 
    min-width: 160px; 
    height: 48px; 
    font-size: 16px; 
  }
  </style>